import Link from "next/link";

export default function SiteFooter() {
  return (
    <footer className="glass border-t border-white/20 mt-20">
      <div className="container mx-auto px-6 py-12">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
          <div>
            <div className="flex items-center space-x-3 mb-6">
              <div className="w-8 h-8 gradient-primary rounded-xl shadow-glow" />
              <span className="text-xl font-bold bg-gradient-to-r from-primary to-primary-dark bg-clip-text text-transparent">
                Finance CV AI
              </span>
            </div>
            <p className="text-muted-foreground leading-relaxed">
              Générateur de CV professionnel pour le secteur financier. Crée des CV élégants et
              impactants en quelques minutes.
            </p>
          </div>
          <div>
            <h3 className="font-bold mb-6 text-foreground">Produit</h3>
            <ul className="space-y-3">
              <li>
                <Link href="/cv" className="text-muted-foreground hover:text-primary transition-colors duration-300 hover:translate-x-1 inline-block">
                  Générateur CV
                </Link>
              </li>
              <li>
                <Link href="/a-propos" className="text-muted-foreground hover:text-primary transition-colors duration-300 hover:translate-x-1 inline-block">
                  À propos
                </Link>
              </li>
            </ul>
          </div>
          <div>
            <h3 className="font-bold mb-6 text-foreground">Ressources</h3>
            <ul className="space-y-3">
              <li>
                <a href="#" className="text-muted-foreground hover:text-primary transition-colors duration-300 hover:translate-x-1 inline-block">
                  Guide ATS
                </a>
              </li>
              <li>
                <a href="#" className="text-muted-foreground hover:text-primary transition-colors duration-300 hover:translate-x-1 inline-block">
                  Exemples de CV
                </a>
              </li>
            </ul>
          </div>
          <div>
            <h3 className="font-bold mb-6 text-foreground">Contact</h3>
            <ul className="space-y-3">
              <li>
                <a href="mailto:hello@financecv.ai" className="text-muted-foreground hover:text-primary transition-colors duration-300 hover:translate-x-1 inline-block">Email</a>
              </li>
            </ul>
          </div>
        </div>
        <div className="border-t border-white/20 mt-12 pt-8 text-center">
          <p className="text-muted-foreground">© {new Date().getFullYear()} Finance CV AI. Tous droits réservés.</p>
        </div>
      </div>
    </footer>
  );
}

