import Link from "next/link";

export default function SiteHeader() {
  return (
    <header className="glass border-b border-white/20 sticky top-0 z-50">
      <div className="container mx-auto px-6 py-4">
        <div className="flex items-center justify-between">
          <Link href="/" className="flex items-center space-x-3 group">
            <div className="w-10 h-10 gradient-primary rounded-xl shadow-glow group-hover:shadow-floating transition-all duration-300" />
            <span className="text-2xl font-bold bg-gradient-to-r from-primary to-primary-dark bg-clip-text text-transparent">
              Finance CV AI
            </span>
          </Link>

          <nav className="hidden md:flex items-center space-x-8">
            <Link href="/" className="text-muted-foreground hover:text-primary font-medium transition-all duration-300 relative group">
              Accueil
              <span className="absolute -bottom-1 left-0 w-0 h-0.5 bg-primary transition-all duration-300 group-hover:w-full" />
            </Link>
            <Link href="/a-propos" className="text-muted-foreground hover:text-primary font-medium transition-all duration-300 relative group">
              À propos
              <span className="absolute -bottom-1 left-0 w-0 h-0.5 bg-primary transition-all duration-300 group-hover:w-full" />
            </Link>
            <Link href="/cv" className="text-muted-foreground hover:text-primary font-medium transition-all duration-300 relative group">
              Générateur
              <span className="absolute -bottom-1 left-0 w-0 h-0.5 bg-primary transition-all duration-300 group-hover:w-full" />
            </Link>
          </nav>

          <Link
            href="/cv"
            className="inline-flex items-center px-4 py-2 rounded-xl bg-slate-900 text-white shadow-elegant hover:shadow-floating transition-all"
          >
            Créer mon CV
          </Link>
        </div>
      </div>
    </header>
  );
}

